@media (min-width: 320px) and (max-width: 480px) { /* SMARTPHONES */
  .app {
    background-image: url(/temperature-app/static/media/weather.c85a30cf.jpg);
    background-size: cover;
    background-position: bottom;
    transition: all 3s ease;
  }

  .app.cold {
    background-image: url(/temperature-app/static/media/phone-cold-bg.a3f3f533.jpg);
    background-size: cover;
    background-position: bottom;
    transition: all 3s ease;
  }
  
  .app.warm {
    background-image: url(/temperature-app/static/media/phone-warm-bg.a7f83012.jpg);
    background-size: cover;
    background-position: bottom;
    transition: all 3s ease;
  }

  .visit-me {
    display: none !important;
  }
}

@media (min-width: 481px) { /* TABLETS AND PCs */
  .app {
    background-image: url(/temperature-app/static/media/weather.c85a30cf.jpg);
    background-size: cover;
    background-position: bottom;
    transition: all 3s ease;
  }

  .app.cold {
    background-image: url(/temperature-app/static/media/cold-bg.fa5aa7b3.jpg);
    background-size: cover;
    background-position: bottom;
  }
  
  .app.warm {
    background-image: url(/temperature-app/static/media/warm-bg.cea9591d.jpg);
    background-size: cover;
    background-position: bottom;
    transition: all 3s ease;
  }

  .visit-me {
    position: absolute;
    bottom: 5%;  
  }
  
  .visit-me-link {
    font-size: 15px;
    font-weight: 600;
    font-family: 'Spartan', sans-serif !important;
    background-color: black;
    color: white;
    text-decoration: none;
    padding: 1rem;
    text-transform: uppercase;
    transition: all .2s ease-in;
  }
  
  .visit-me-link:hover {
    padding-left: 30px;
  }
}

